.scrollup {
  width: 50px;
  height: 50px;
  background-color: var(--accent-color);
  z-index: 999999;
  border-radius: 50%;
  outline: none;
  position: fixed;
  bottom: 50px;
  right: 20px;
}

.scrollup:after {
  content: "";
  width: 20px;
  height: 20px;
  border-top: 5px solid var(--background-color);
  border-left: 5px solid var(--background-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -30%)rotate(45deg);
}

