@media screen and (max-width: 767px) {
    // .products {
    //     background-color: #F5F5F5;
    // }
    .container-products{
        padding-top: 60px;
        padding-bottom: 66px;
    }
    .products {
        background-image: url('../images/mobile/bg-1.jpg'), url('../images/mobile/bg-2.jpg'), url('../images/mobile/bg-3.jpg');
        background-position: top 0px right 0px, top 700px right 0px, top 1267px right 0px;
        // background-repeat: no-repeat, no-repeat, no-repeat;
        max-width: 100%;
        background-size: contain;

        }

    .name-ice-cream::before {
        content: '';
        display: block;
        position: absolute;
        width: 200px;
        height: 200px;
        left: 42px;
        top: -100px;
        border-radius: 50%;
        background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
        box-shadow: 0px 8px 30px rgba(10, 136, 23, 0.25);

    }

    .name-ice-cream::after {
        content: '';
        background-image: url('../images/mobile/product1.png');
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        width: 200px;
        height: 200px;
        top: -112px;
        left: 42px;
        border-radius: 50%;
    }
        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            .name-ice-cream::after {
                background-image: url('../images/mobile/product1@2x.png')
            }
        }

