    }

    .name-ice-coffee::after {
        content: '';
        background-image: url('../images/mobile/product2.png');
       
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        width: 200px;
        height: 200px;
        top: -112px;
        left: 42px;
        border-radius: 50%;
    }
    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
            .name-ice-coffee::after {
                 background-image: url('../images/mobile/product2@2x.png')
             }
         }

    .name-milkshake::before {
        content: '';
        display: block;
        position: absolute;
        width: 200px;
        height: 200px;
        left: 42px;
        top: -100px;
        border-radius: 50%;
        background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #D9D9D9;
        box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
    }

    .name-milkshake::after {
        content: '';
        background-image: url('../images/mobile/product3.png');
        
                
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        width: 200px;
        height: 200px;
        top: -112px;
        left: 42px;
        border-radius: 50%;
    }
        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            .name-milkshake::after{
                background-image: url('../images/mobile/product3@2x.png')
            }
        }
    .products-list__item{
        padding: 140px 40px 28px;
        border-radius: 24px;
        position: relative;
    }

    .products-text {
        font-size: 18px;
    }

    .products-title {
        font-size: 30px;
    }
    .text-box {
        min-height: 234px;
    }
   

    .products-list__item:not(:last-child) {
        margin-bottom: 164px;
    }

    .card-name {
        font-size: 20px;

    }

    .card-text {
        font-size: 14px;
    }
    .products-title {
        margin-bottom: 148px;
    }
}
@media screen and (min-device-pixel-ratio: 2) and (max-width:767px),
screen and (min-resolution: 192dpi)  and (max-width:767px),
screen and (min-resolution: 2dppx)  and (max-width:767px) {
    .products {
        background-image: url('../images/mobile/bg-1@2x.jpg'), url('../images/mobile/bg-2@2x.jpg'),
            url('../images/mobile/bg-3@2x.jpg');
    }
}

.products {
    margin-left: auto;
    margin-right: auto;
}

.products-text {
    font-weight: 800;
    font-size: 18px;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    color: #131313;
    margin-bottom: 12px;
}

.products-title {
    font-family: var(--title-font);
    font-weight: 400;
    font-size: 30px;
    line-height: 1.26;
    text-align: center;
    text-transform: uppercase;
    color: #131313;
}


.ice-cream {
    background-color: #AEC6A5;
}

.ice-coffee {
    background-color: #EFD478;
}

.milkshake {
    background-color: #D6936D;
}

.card-name {
    font-weight: 700;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    color: #131313;
    margin-bottom: 40px;
}

.card-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.8;
    text-align: center;
    color: #131313;
    margin-bottom: 60px;
    // flex-grow: 1;
}
.text-box{
    display: flex;
   flex-direction: column;
   justify-content: space-between;
   height: 100%;
}


.btn-arrow {
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    margin-right: auto;
    margin-left: auto;
    cursor: pointer;
    border-radius: 50%;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        transform: scale(1.2);
    }
}

.card-icon {
    width: 40px;
    height: 15px;
        
}


